import { initializeApp } from "firebase/app";
import { getDatabase, ref, child, get } from "firebase/database";
import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import { firebaseConfig } from './../appinfo'  



const app = initializeApp(firebaseConfig);
const dbRef = ref(getDatabase(app));
function Home(){
  const [userComponents, setUserComponents] = useState([]);
  const navigate = useNavigate();
  var user = localStorage.getItem('user');
  if(user === undefined ){
    console.log('notit');
    navigate('/login', { replace: true });
  } else if (user === null){
    console.log('notit');
    navigate('/login', { replace: true });
  }

  useEffect(() => {
    const fetchData = async () => {
      const snapshot = await get(child(dbRef, `workersArr`));
      if (snapshot.exists()) {
        const name1 = snapshot.val();
        const promises = snapshot.val().map(async (item) => {
          const itemSnapshot = await get(child(dbRef, `workers/${item}/working`));
          return <User name={item} bool1={itemSnapshot.val()} />;
        });
        const components = await Promise.all(promises);
        setUserComponents(components);
      }
    };

    fetchData().catch((error) => {
      console.error(error);
    });
  }, []);
    return( 
      <div>
      <h1 style={{ fontFamily: 'monospace', fontSize: 36, textAlign: 'center' }}>
        Pozdravljen Doma
      </h1>
      <div style={{ overflow: 'auto', height: 650 }}>
        {userComponents}
        
        {/* <User bool1={false} name='Svit' /> */}
      </div>
      <div style={{ margin: 'auto', padding: 10, width: 100 }}>
        <div onClick={()=> {navigate('/add?admin=' + user)}} style={{
          width: 75,
          height: 75,
          backgroundColor: 'yellow',
          cursor: 'pointer',
          borderRadius: 100,
        }}>
          <h1 style={{
            fontFamily: 'monospace',
            fontSize: 50,
            fontWeight: 'bold',
            textAlign: 'center',
            paddingTop: 5,
            userSelect: 'none',
          }}>+</h1>
        </div>
      </div>
    </div>)
}

function User(props){
  var working = props.bool1;
  const navigate = useNavigate();
  function onClick(){
    console.log('asdasd');
    navigate('/user?name=' + props.name);
  }
  return(
    <div onClick={onClick}>
    <div style={{ borderWidth: 3, borderColor: 'black', borderStyle: 'solid', userSelect: 'none', cursor: 'pointer' }}>
      <h1 style={{ textAlign: 'center', fontSize: 36, fontFamily: 'monospace' }}>
        {props.name}
      </h1>
      <h1 style={{
        textAlign: 'center',
        fontSize: 23,
        fontFamily: 'monospace',
        color: working ? 'green' : 'red',
        marginTop: -20,
      }}>
        {working ? 'Dela' : 'Ne Dela'}
      </h1>
    </div>
    <div style={{height: 10}}></div>
    </div>
  )

}
export default Home;