import { initializeApp } from "firebase/app";
import { getDatabase, ref, child, get , set} from "firebase/database";
import React, { useState, useEffect,   } from 'react';
import { useNavigate, useParams, Route, useLocation } from "react-router-dom";
import { firebaseConfig } from './../appinfo'  


const app = initializeApp(firebaseConfig);
const dbRef = ref(getDatabase(app));
const db = getDatabase();
function Year(props){
  const [isShowed, setIsShowed] = useState(false)
  const navigate = useNavigate();
  function onClickMonthly(){
    console.log('asdasd');
    navigate('/months?name=' + props.name + '&year=' + props.year + '&bool=0');
  }
  function onClickYearly(){
    navigate('/months?name=' + props.name + '&year=' + props.year + '&bool=1');
  }
  function onClick(){
    if(isShowed){
      setIsShowed(false)
    } else{
      setIsShowed(true)
    }
    
  }
    return(<div style={{width: '90%', borderRadius: 200, backgroundColor: 'gray', margin: 'auto', userSelect: 'none', cursor: 'pointer'}} onClick={onClick}>
        <h1 style={{
            textAlign: 'center',
            fontSize: 36,
            fontFamily: 'monospace',
          }}>{props.year}</h1>
        {isShowed&& <div>
          <h1 style={{
            textAlign: 'center',
            fontSize: 30,
            fontFamily: 'monospace',
            color: "blue",
            cursor: "pointer"
          }} onClick={onClickMonthly}>Naloi meseno poroilo</h1>
                    <h1 style={{
            textAlign: 'center',
            fontSize: 30,
            fontFamily: 'monospace',
            color: "blue",
            cursor: "pointer"
          }} onClick={onClickYearly}>Naloi letno poroilo</h1>
        </div>

        }
    </div>)
}



function Main1(){
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const name = searchParams.get('name');
    var data = {};
    const [key1, setKey1] = useState([]);
    const [working, setWorking] = useState(false);
    const [checked, setChecked] = useState(false);
    
    useEffect(()=>{
      console.log('asfkjaghagkh ' + name);
      get(child(dbRef, `workers/${name}/data`)).then((snapshot) => {
        if (snapshot.exists()) {
          data = snapshot.val();
          setKey1(Object.keys(data))
          console.log('smt ' + key1);
        } else {
          setKey1([])
        }
          get(child(dbRef, `workers/${name}/working`)).then((snapshot) => {
            if (snapshot.exists()) {
              setWorking(snapshot.val())
              get(child(dbRef, `workers/${name}/nfc`)).then((snapshot) => {
                console.log('sdsdasadasd   ' + snapshot.val())
                setChecked(snapshot.val());
              }).catch((error) => {
                console.error(error);
              });
            } else {
      
            }
          }).catch((error) => {
            console.error(error);
          });

      }).catch((error) => {
        console.error(error);
      });
    }, [])

    const handleChange = (event) => {
      const isChecked = event.target.checked;
      setChecked(isChecked);
      set(ref(db, `workers/${name}/nfc`), isChecked);
    };
    
    const checkboxStyles = {
      container: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center', // Center horizontally
        cursor: 'pointer',
        fontSize: '16px',
        color: '#333',
      },
      checkbox: {
        fontFamily: 'monospace',
        position: 'relative',
        width: '20px',
        height: '20px',
        border: '2px solid #777',
        borderRadius: '3px',
        marginRight: '10px',
        //TODO
        backgroundColor: checked ? '#4CAF50' : 'transparent',
      },
      checkmark: {
        fontFamily: 'monospace',
        position: 'absolute',
        //TODO
        display: checked ? 'block' : 'none',
        left: '6px',
        top: '1px',
        width: '5px',
        height: '10px',
        border: 'solid white',
        borderWidth: '0 2px 2px 0',
        transform: 'rotate(45deg)',
      },
    };
  


      
    return(
        <div >
            <h1 style={{
            textAlign: 'center',
            fontSize: 72,
            fontFamily: 'monospace',
          }}
          >{name}</h1>
                  <div style={{marginTop: -50}}>
        <label style={checkboxStyles.container}>
        <input
        type="checkbox"
        checked={checked}
        onChange={handleChange}
        style={checkboxStyles.checkbox}
      />
      <span style={checkboxStyles.checkmark}></span>
      <h1 style={{ fontSize: 20, fontFamily: 'monospace'}}>Zahtevaj nfc?</h1>
    </label>
        </div>
        <h1 style={{
            textAlign: 'center',
            fontSize: 23,
            fontFamily: 'monospace',
            color: working ? 'green' : 'red',
            marginTop: -10,
          }}>
            {working ? 'Dela' : 'Ne Dela'}
          </h1>
          <div style={{width: '75%', backgroundColor: '#DCDCDC', height: 550, margin: 'auto', padding: 10, borderRadius: 50, overflow:'auto'}}>
            <h1 style={{textAlign: 'center', fontSize: 36, fontFamily: 'monospace', marginTop: 0}}>Leto</h1>
            {/* <Year year= '2009' int='8' /> */}
            {
            key1.map((item, index)=>{


                  return <Year year= {item} name={name} />
              
          })
        }
          </div>
        </div>


    )
}



export default Main1;