import { initializeApp } from "firebase/app";
import { getDatabase, ref, child, get } from "firebase/database";
import React, { useState } from 'react';
import { firebaseConfig } from './../appinfo'  


const app = initializeApp(firebaseConfig);
const dbRef = ref(getDatabase(app));
const LoginScreen = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    // Perform login logic here with email and password
    console.log('Email:', email);
    console.log('Password:', password);
    get(child(dbRef, `users/${email}/pass`)).then((snapshot) => {
      if (snapshot.exists()) {
        if(password === snapshot.val()){
          alert('prijavljen');
          localStorage.setItem('user', email);
        } else{
          alert('Narobe geslo');
        }
      } else {
        alert('Uporabnik ne obstaja');
      }
    }).catch((error) => {
      console.error(error);
    });
  };

  return (
    <div className="login-container" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      <h1 style={{ marginBottom: '1rem' }}>Prijava</h1>
      <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <input
          type="name"
          placeholder="Ime"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          style={{ marginBottom: '0.5rem', padding: '0.5rem' }}
        />
        <input
          type="password"
          placeholder="Geslo"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          style={{ marginBottom: '0.5rem', padding: '0.5rem' }}
        />
        <button type="submit" style={{ padding: '0.5rem 1rem', backgroundColor: 'blue', color: 'white' }}>
          Nadeljuj
        </button>
      </form>
    </div>
  );
};

export default LoginScreen;
