import React, { useState } from 'react';

const styles = {
  ul: {
    listStyleType: 'none',
    padding: 0,
    marginLeft: 20,
  },
  li: {
    marginBottom: 10,
  },
  input: {
    marginRight: 10,
  },
};

const RealTimeDatabase = () => {
  const [data, setData] = useState({
    exampleKey: 'exampleValue',
    nestedObject: {
      key1: 'value1',
      key2: 'value2',
    },
  });

  const [collapsedKeys, setCollapsedKeys] = useState({});

  const handleChange = (e, key) => {
    const { value } = e.target;
    setData((prevState) => ({
      ...prevState,
      [key]: value,
    }));
  };

  const handleDelete = (key) => {
    setData((prevState) => {
      const newState = { ...prevState };
      delete newState[key];
      return newState;
    });
  };

  const handleAdd = (key, hasChildren) => {
    const value = hasChildren ? {} : '';
    setData((prevState) => ({
      ...prevState,
      [key]: value,
    }));
  };

  const handleCollapseToggle = (key) => {
    setCollapsedKeys((prevCollapsedKeys) => ({
      ...prevCollapsedKeys,
      [key]: !prevCollapsedKeys[key],
    }));
  };

  const renderNestedData = (obj, parentKey = null) => {
    return (
      <ul style={styles.ul}>
        {Object.entries(obj).map(([key, value]) => (
          <li key={key} style={styles.li}>
            <span>
              {parentKey && `${parentKey}.`}
              {key}:
            </span>
            {typeof value === 'object' ? (
              <button onClick={() => handleCollapseToggle(key)}>
                {collapsedKeys[key] ? 'Expand' : 'Collapse'}
              </button>
            ) : (
              <input
                type="text"
                style={styles.input}
                value={value}
                onChange={(e) => handleChange(e, key)}
              />
            )}
            <button onClick={() => handleDelete(key)}>Delete</button>
            {!collapsedKeys[key] && typeof value === 'object' && renderNestedData(value, key)}
          </li>
        ))}
      </ul>
    );
  };

  return (
    <div>
      <h1>Real Time Database</h1>
      {renderNestedData(data)}
      <button onClick={() => handleAdd('newKey', false)}>Add Value</button>
    </div>
  );
};

export default RealTimeDatabase;
