import { initializeApp } from "firebase/app";
import { getDatabase, ref, child, get, set } from "firebase/database";
import React, { useState, useEffect ,} from 'react';
import { useLocation,useNavigate } from "react-router-dom";
import { firebaseConfig } from './../appinfo'  


const app = initializeApp(firebaseConfig);
const dbRef = ref(getDatabase(app));
const db = getDatabase();



function Main() {
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const admin = searchParams.get('admin');
  const [name, setName] = useState('');
  const [surname, setSurname] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [checked, setChecked] = useState(true);
  const [time, setTime] = useState('08:00:00');
  const navigate = useNavigate();


  function isValidTimeFormat(input) {
    // Regular expression to match the hh/mm/ss format
    const regex = /^(0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]:[0-5][0-9]$/;
  
    // Test the input against the regex
    return regex.test(input);
  }
  
  const handleTimeChange = (newTime) => {
    setTime(newTime);
  };


  const handleChange = (event) => {
    const isChecked = event.target.checked;
    setChecked(isChecked);
  };

  function createUser() {
    
    if (name === '' || surname === '' || email === '' || password === '' || time === '') {
      alert('Upii podatke v vsa polja');
    } else {
      get(child(dbRef, `workers/${name}`))
        .then((snapshot) => {
          if (snapshot.exists()) {
            alert('Uporabnik z tem uporabnikim imenom e obstaja, prosimo da pomislite na drugo ime');
          } else {
            if(isValidTimeFormat(time)){
              set(ref(db, `workers/${name}`), {
                code: password,
                email: email,
                fullname: surname,
                working: false,
                break: false,
                nfc: checked,
                ure: time
              });
              get(child(dbRef, `workersArr`)).then((snapshot) => {
                  if (snapshot.exists()) {
                      var arr1 = [...snapshot.val(), name]
                      set(ref(db, 'workersArr'), arr1);
                      
                  } else {
                      set(ref(db, 'workersArr'), [name]);
                  }
                }).catch((error) => {
                  console.error(error);
                });
                navigate('/', { replace: true });
            } else{
              alert('Format za delavni as se ne ujema poskusite uporabiti uu:mm:ss, u: ura, m:minute, s: sekunde')
            }

          }
        })
        .catch((error) => {
          console.error(error);
        });
    }
  }

  const checkboxStyles = {
    container: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center', // Center horizontally
      cursor: 'pointer',
      fontSize: '16px',
      color: '#333',
      marginBottom: '10px',
    },
    checkbox: {
      fontFamily: 'monospace',
      position: 'relative',
      width: '20px',
      height: '20px',
      border: '2px solid #777',
      borderRadius: '3px',
      marginRight: '10px',
      //TODO
      backgroundColor: checked ? '#4CAF50' : 'transparent',
    },
    checkmark: {
      fontFamily: 'monospace',
      position: 'absolute',
      //TODO
      display: checked ? 'block' : 'none',
      left: '6px',
      top: '1px',
      width: '5px',
      height: '10px',
      border: 'solid white',
      borderWidth: '0 2px 2px 0',
      transform: 'rotate(45deg)',
    },
  };


  return (
    <div>
      <h1
        style={{
          textAlign: 'center',
          fontSize: 72,
          fontFamily: 'monospace',
        }}
      >
        Dodaj delavca
      </h1>
      <h1
        style={{
          textAlign: 'center',
          fontSize: 23,
          fontFamily: 'monospace',
          marginTop: -20,
        }}
      >
        {admin}
      </h1>
      <div
        style={{
          width: '75%',
          backgroundColor: '#DCDCDC',
          height: 550,
          margin: 'auto',
          padding: 10,
          borderRadius: 50,
          overflow: 'auto',
        }}
      >
        <h1 style={{ textAlign: 'center', fontSize: 36, fontFamily: 'monospace', marginTop: 0 }}>
          Podatki o delavcu
        </h1>
        <h1 style={{ textAlign: 'center', fontSize: 20, fontFamily: 'monospace', marginTop: 0 }}>Email</h1>
        <div style={{ width: '50%', margin: 'auto', display: 'flex', alignItems: 'center' }}>
          <input
            type='email'
            style={{ borderWidth: 0, borderRadius: 30, width: 200, height: 35, textAlign: 'center', margin: 'auto' }}
            placeholder="nekdo@nekaj.si"
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>

        <h1 style={{ textAlign: 'center', fontSize: 20, fontFamily: 'monospace' }}>Uporabniko Ime</h1>
        <div style={{ width: '50%', margin: 'auto', display: 'flex', alignItems: 'center', marginTop: -10 }}>
          <input
            type='name'
            style={{ borderWidth: 0, borderRadius: 30, width: 200, height: 35, textAlign: 'center', margin: 'auto' }}
            placeholder="nekdo123"
            onChange={(e) => setName(e.target.value)}
          />
        </div>
        <h1 style={{ textAlign: 'center', fontSize: 20, fontFamily: 'monospace' }}>Ime in priimek</h1>
        <div style={{ width: '50%', margin: 'auto', display: 'flex', alignItems: 'center', marginTop: -10 }}>
          <input
            type='name'
            style={{ borderWidth: 0, borderRadius: 30, width: 200, height: 35, textAlign: 'center', margin: 'auto' }}
            placeholder="nekdo"
            onChange={(e) => setSurname(e.target.value)}
          />
        </div>
        <h1 style={{ textAlign: 'center', fontSize: 20, fontFamily: 'monospace', marginTop: 0 }}>Geslo</h1>
        <div style={{ width: '50%', margin: 'auto', display: 'flex', alignItems: 'center', marginTop: -10 }}>
          <input
            type='text'
            style={{ borderWidth: 0, borderRadius: 30, width: 200, height: 35, textAlign: 'center', margin: 'auto' }}
            placeholder="*******"
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <div>
        <h1 style={{ textAlign: 'center', fontSize: 20, fontFamily: 'monospace', marginTop: 0 }}>Delavni as</h1>
        <div style={{ width: '50%', margin: 'auto', display: 'flex', alignItems: 'center', marginTop: -10 }}>
          <input
            type='text'
            style={{ borderWidth: 0, borderRadius: 30, width: 200, height: 35, textAlign: 'center', margin: 'auto' }}
            placeholder="hh:mm:ss"
            onChange={(e) => setTime(e.target.value)}
            value={time}
          />
        </div>
        <label style={checkboxStyles.container}>
        <input
        type="checkbox"
        checked={checked}
        onChange={handleChange}
        style={checkboxStyles.checkbox}
      />
      <span style={checkboxStyles.checkmark}></span>
      <h1 style={{ fontSize: 20, fontFamily: 'monospace'}}>Zahtevaj nfc?</h1>
    </label>
        </div>
        
        <div style={{ marginTop: 20 }}>
          <div
            style={{
              height: 50,
              width: 150,
              backgroundColor: 'green',
              borderRadius: 20,
              borderStyle: 'solid',
              margin: 'auto',
              cursor: 'pointer',
              userSelect: 'none',
            }}
            onClick={createUser}
          >
            <h1 style={{ textAlign: 'center', fontSize: 36, fontFamily: 'monospace', marginTop: 0 }}>Naprej</h1>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Main;
