import { initializeApp } from "firebase/app";
import { getDatabase, ref, child, get } from "firebase/database";
import React, { useState, useEffect,  } from 'react';
import { useParams, useLocation, useNavigate } from "react-router-dom";
import { firebaseConfig } from './../appinfo';
import * as XLSX from 'xlsx';

const app = initializeApp(firebaseConfig);
const dbRef = ref(getDatabase(app));

function calculateTotalTime(start, end) {
  const startTime = new Date();
  const [startHour, startMinute, startSecond] = start.split(':');
  startTime.setHours(parseInt(startHour), parseInt(startMinute), parseInt(startSecond), 0);

  const endTime = new Date();
  const [endHour, endMinute, endSecond] = end.split(':');
  endTime.setHours(parseInt(endHour), parseInt(endMinute), parseInt(endSecond), 0);

  const timeDiff = endTime - startTime;

  const hours = Math.floor(timeDiff / (1000 * 60 * 60));
  const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);
  
  return `${hours}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
}

function addTimeIntervals(interval1, interval2) {
  const [hours1, minutes1, seconds1] = interval1.split(':').map(Number);
  const [hours2, minutes2, seconds2] = interval2.split(':').map(Number);

  const totalSeconds = (hours1 * 3600 + minutes1 * 60 + seconds1) + (hours2 * 3600 + minutes2 * 60 + seconds2);
  
  const hours = Math.floor(totalSeconds / 3600);
  const minutes = Math.floor((totalSeconds % 3600) / 60);
  const seconds = totalSeconds % 60;

  return `${hours}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
}
function compareTimeAndDisplayDifference(time1, time2) {
  const [hours1, minutes1, seconds1] = time1.split(':').map(Number);
  const [hours2, minutes2, seconds2] = time2.split(':').map(Number);

  const totalSeconds1 = hours1 * 3600 + minutes1 * 60 + seconds1;
  const totalSeconds2 = hours2 * 3600 + minutes2 * 60 + seconds2;

  const differenceInSeconds = totalSeconds2 - totalSeconds1;

  let comparison;
  if (differenceInSeconds < 0) {
    comparison = "+";
  } else if (differenceInSeconds > 0) {
    comparison = "-";
  } else {
    comparison = "";
  }

  const absoluteDifference = Math.abs(differenceInSeconds);
  const hours = Math.floor(absoluteDifference / 3600);
  const minutes = Math.floor((absoluteDifference % 3600) / 60);
  const seconds = absoluteDifference % 60;

  const formattedDifference = `${comparison}${hours}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  return formattedDifference;
}

function subtractTime(time1, time2) {
  const [hours1, minutes1, seconds1] = time1.split(':').map(Number);
  const [hours2, minutes2, seconds2] = time2.split(':').map(Number);

  const totalSeconds1 = hours1 * 3600 + minutes1 * 60 + seconds1;
  const totalSeconds2 = hours2 * 3600 + minutes2 * 60 + seconds2;

  const differenceInSeconds = totalSeconds1 - totalSeconds2;

  if (differenceInSeconds < 0) {
    throw new Error("Resulting time cannot be negative.");
  }

  const hours = Math.floor(differenceInSeconds / 3600);
  const minutes = Math.floor((differenceInSeconds % 3600) / 60);
  const seconds = differenceInSeconds % 60;

  const formattedDifference = `${hours}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  return formattedDifference;
}


function adjustTimeBasedOnComparison(time1, time2, inputTime) {
  const [hours1, minutes1, seconds1] = time1.split(':').map(Number);
  const [hours2, minutes2, seconds2] = time2.split(':').map(Number);
  const [inputHours, inputMinutes, inputSeconds] = inputTime.split(':').map(Number);

  const totalSeconds1 = hours1 * 3600 + minutes1 * 60 + seconds1;
  const totalSeconds2 = hours2 * 3600 + minutes2 * 60 + seconds2;

  const differenceInSeconds = totalSeconds2 - totalSeconds1;

  let adjustedTimeInSeconds;
  if (differenceInSeconds < 0) {
    adjustedTimeInSeconds = (inputHours * 3600 + inputMinutes * 60 + inputSeconds) + differenceInSeconds;
  } else if (differenceInSeconds > 0) {
    adjustedTimeInSeconds = (inputHours * 3600 + inputMinutes * 60 + inputSeconds) - Math.abs(differenceInSeconds);
  } else {
    adjustedTimeInSeconds = inputHours * 3600 + inputMinutes * 60 + inputSeconds;
  }

  const adjustedHours = Math.floor(adjustedTimeInSeconds / 3600);
  const adjustedMinutes = Math.floor((adjustedTimeInSeconds % 3600) / 60);
  const adjustedSeconds = adjustedTimeInSeconds % 60;

  const adjustedTime = `${adjustedHours}:${adjustedMinutes.toString().padStart(2, '0')}:${adjustedSeconds.toString().padStart(2, '0')}`;
  return adjustedTime;
}

function addTime(baseTime, intervalTime) {
  const parseTime = (time) => {
    const [hours, minutes, seconds] = time.split(':').map(Number);
    return hours * 3600 + minutes * 60 + seconds;
  };

  const baseSeconds = baseTime === "0:00:00" ? 0 : parseTime(baseTime);
  const intervalSeconds = parseTime(intervalTime);

  const totalSumSeconds = baseSeconds + intervalSeconds;

  const sumHours = Math.floor(totalSumSeconds / 3600);
  const sumMinutes = Math.floor((totalSumSeconds % 3600) / 60);
  const sumSeconds = totalSumSeconds % 60;

  const sumTime = `${sumHours}:${sumMinutes.toString().padStart(2, '0')}:${sumSeconds.toString().padStart(2, '0')}`;
  return sumTime;
}

function Month(props) {
  
  const navigate = useNavigate();
  useEffect(
    ()=> {
      if(props.bool == true){
        yearExport();
      }
    }, []
  )

  const generateSheet = async () => {
    try {
      var dataCSV = [];
      var monthTime = '0:00:00'
      var wb = XLSX.utils.book_new();

      const snapshot1 = await get(child(dbRef, `workers/${props.name}/fullname`));
      const fullname = snapshot1.val();

      const snapshot2 = await get(child(dbRef, `workers/${props.name}/email`));
      const email = snapshot2.val();

      const snapshot3 = await get(child(dbRef, `workers/${props.name}/data/${props.year}/${props.month}`));
      const data = snapshot3.val();
      const key1 = Object.keys(data);

      const snapshot4 = await get(child(dbRef, `workers/${props.name}/ure`));
      const totalTime = snapshot4.val();

      for (const item of key1) {
        let str1 = '';
        let int1 = '';
        let str2 = '';
        let int3 = '';
        var day = item;

        const snapshot4 = await get(child(dbRef, `workers/${props.name}/data/${props.year}/${props.month}/${item}`));
        const data1 = snapshot4.val();
        const startData = data1.time.start;
        const stopData = data1.time.stop;
        var breakStartData = data1.break && data1.break.start || [];
        var breakStopData = data1.break && data1.break.stop || [];
        
        if(data1.time.start == null ){
          breakStartData = []
          breakStopData = []

        }

        

        for (let index = 0; index < startData.length; index++) {
          var int2 = calculateTotalTime(startData[index], stopData[index]);
          if (int1 === '') {
            int1 = int2;
          } else {
            int1 = addTimeIntervals(int1, int2);
          }
          str1 += startData[index] + ' - ' + stopData[index] + ' ; ';
        }
        for (let index = 0; index < breakStartData.length; index++) {
          const int2 = calculateTotalTime(breakStartData[index], breakStopData[index]);
          if (int3 === '') {
            int3 = int2;
          } else {
            int3 = addTimeIntervals(int3, int2);
          }
          str2 += breakStartData[index] + ' - ' + breakStopData[index] + ' ; ';
        }
        var int4 = calculateTotalTime(int3, int1);
        var int5 = compareTimeAndDisplayDifference(int1, totalTime);
        monthTime = addTime(monthTime, int1 === 'NaN:NaN:NaN' ? int1 : int1);
  
        dataCSV.push({
          "Ime in priimek": fullname,
          "Email": email,
          "Leto": props.year,
          "Mesec": props.month,
          "Dan": day,
          "Od Do Delavnega asa": str1,
          "Od Do asa za malico": str2,
          'Skupni as malice': int3,
          'Skupni as': int1,
          'Preseen ali nedoseen as': int5 === 'NaN:NaN:NaN' ? '' : int5,
          'Setevek ur': monthTime
        });
      
      }

      console.log('all data: ' + JSON.stringify(dataCSV));
      var ws = XLSX.utils.json_to_sheet(dataCSV);

      XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

      XLSX.writeFile(wb, 'userData.xlsx');
    } catch (error) {
      console.error(error);
    }
  };

  async function yearExport() {
    const snapshot3 = await get(child(dbRef, `workers/${props.name}/data/${props.year}`));
    const yearData = snapshot3.val();
    const yearDataKeys = Object.keys(yearData);
  
    var wb = XLSX.utils.book_new();
  
    for (const item of yearDataKeys) {
      try {
        var object = await generateSheetYear({ name: props.name, year: props.year, month: item });
        console.log('slayy' + object);
        var ws = XLSX.utils.json_to_sheet(object);
        XLSX.utils.book_append_sheet(wb, ws, item + '. Mesec');
      } catch (error) {
        console.error(error);
      }
    }
  
    XLSX.writeFile(wb, 'YearlyUserData.xlsx');
    navigate('/user?name=' + props.name);
  }

  const generateSheetYear = async (props) => {
    try {
      var dataCSV = [];
      var monthTime = '0:00:00';
      var wb = XLSX.utils.book_new();
  
      const snapshot1 = await get(child(dbRef, `workers/${props.name}/fullname`));
      const fullname = snapshot1.val();
  
      const snapshot2 = await get(child(dbRef, `workers/${props.name}/email`));
      const email = snapshot2.val();
  
      const snapshot3 = await get(child(dbRef, `workers/${props.name}/data/${props.year}/${props.month}`));
      const data = snapshot3.val();
        
      const key1 = Object.keys(data);
  
      const snapshot4 = await get(child(dbRef, `workers/${props.name}/ure`));
      const totalTime = snapshot4.val();
  
      for (const item of key1) {
        let str1 = '';
        let int1 = '';
        let str2 = '';
        let int3 = '';
        var day = item;
  
        const snapshot4 = await get(child(dbRef, `workers/${props.name}/data/${props.year}/${props.month}/${item}`));
        const data1 = snapshot4.val();
        const startData = data1.time.start;
        const stopData = data1.time.stop;
        var breakStartData = data1.break && data1.break.start || [];
        var breakStopData = data1.break && data1.break.stop || [];
          
        if(data1.time.start == null ){
          breakStartData = [];
          breakStopData = [];
        }
  
        for (let index = 0; index < startData.length; index++) {
          var int2 = calculateTotalTime(startData[index], stopData[index]);
          if (int1 === '') {
            int1 = int2;
          } else {
            int1 = addTimeIntervals(int1, int2);
          }
          str1 += startData[index] + ' - ' + stopData[index] + ' ; ';
        }
        
        for (let index = 0; index < breakStartData.length; index++) {
          const int2 = calculateTotalTime(breakStartData[index], breakStopData[index]);
          if (int3 === '') {
            int3 = int2;
          } else {
            int3 = addTimeIntervals(int3, int2);
          }
          str2 += breakStartData[index] + ' - ' + breakStopData[index] + ' ; ';
        }
        
        var int4 = calculateTotalTime(int1, int1);
        var int5 = compareTimeAndDisplayDifference(int1, totalTime);
        monthTime = addTime(monthTime, int1 === 'NaN:NaN:NaN' ? int1 : int1);
  
        dataCSV.push({
          "Ime in priimek": fullname,
          "Email": email,
          "Leto": props.year,
          "Mesec": props.month,
          "Dan": day,
          "Od Do Delavnega asa": str1,
          "Od Do asa za malico": str2,
          'Skupni as malice': int3,
          'Skupni as': int1,
          'Preseen ali nedoseen as': int5 === 'NaN:NaN:NaN' ? '' : int5,
          'Setevek ur': monthTime
        });
      }
      
      console.log(dataCSV);
      return dataCSV;
    } catch (error) {
      console.error(error);
      throw error; // Re-throw the error to be caught by the caller
    }
  };

  return (
    
    <div
      style={{
        width: '90%',
        borderRadius: 20,
        backgroundColor: 'gray',
        margin: 'auto',
        userSelect: 'none',
        cursor: 'pointer',
      }}
      onClick={generateSheet}
    >
      <h1
        style={{
          textAlign: 'center',
          fontSize: 36,
          fontFamily: 'monospace',
        }}
      >
        {props.month}
      </h1>
    </div>
  );
}

function Main() {
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const name = searchParams.get('name');
  const year = searchParams.get('year');
  const bool = searchParams.get('bool') == '1'? true : false;
  const [key1, setKey1] = useState([]);
  

  useEffect(() => {
    get(child(dbRef, `workers/${name}/data/${year}`))
      .then((snapshot) => {
        if (snapshot.exists()) {
          setKey1(Object.keys(snapshot.val()));
        } else {
          console.log("No data available");
        }
      })
      .catch((error) => {
        console.error(error);
      });
  }, [dbRef, name, year]);
  return (
    
    <div>
      <h1
        style={{
          textAlign: 'center',
          fontSize: 72,
          fontFamily: 'monospace',
        }}
      >
        {name}
      </h1>
      <h1
        style={{
          textAlign: 'center',
          fontSize: 23,
          fontFamily: 'monospace',
          marginTop: -20,
        }}
      >
        {year}
      </h1>
      <div
        style={{
          width: '75%',
          backgroundColor: '#DCDCDC',
          height: 550,
          margin: 'auto',
          padding: 10,
          borderRadius: 50,
          overflow: 'auto',
        }}
      >
        <h1
          style={{
            textAlign: 'center',
            fontSize: 36,
            fontFamily: 'monospace',
            marginTop: 0,
          }}
        >
          Mesec
        </h1>
        {key1.map((item) => (
          <Month key={item} month={item} year={year} name={name} bool={bool} />
        ))}
      </div>
    </div>
        );
}

export default Main;
